/* footer section css start */
.footer {
  background-color: $base-color-two;
  position: relative;
  z-index: 1;
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.75;
    z-index: -1; }
  &__top {
    padding-top: 100px;
    padding-bottom: 100px;
    border-top: 1px solid rgba(#fff, 0.15); }
  &__bottom {
    padding: rem(15px) 0;
    position: relative;
    z-index: 1;
    &::before {
      @include positionTwo;
      background-color: darken($base-color-two, 5%);
      opacity: 0.65;
      z-index: -1; } } }
.social-links-list {
  @include d-flex;
  margin: -5px -8px;
  li {
    margin: 5px 8px;
    a {
      width: 40px;
      height: 40px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background-color: $base-color;
      color: #fff;
      @include border-radius(50%); } } }
.footer-short-list {
  margin: -5px -10px;
  li {
    margin: 5px 10px;
    a {
      color: $para-color;
      &:hover {
        color: $base-color; } } } }
.scroll-to-top {
  height: rem(45px);
  width: rem(45px);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50%;
  background-color: $base-color;
  box-shadow: 0 5px 15px 0 rgba(#000000, 0.25);
  font-size: rem(24px);
  margin-top: -20px;
  @include transform(translateY(-20px)); }
/* footer section css end */
