/* hero section css start */
.hero-slider {
  .single-slide {
    padding-top: 280px;
    padding-bottom: 220px;
    background-position-x: center;
    background-position-y: 0;
    @include media(1199px) {
      padding-top: 200px;
      padding-bottom: 140px; } }
  .slick-dots {
    position: absolute;
    bottom: 50px;
    left: 0;
    width: 100%;
    @include d-flex;
    justify-content: center;
    align-items: center;
    li {
      margin: 5px 7px;
      &.slick-active {
        button {
          background-color: $base-color; } }
      button {
        width: 15px;
        height: 15px;
        background-color: rgba(#fff, 0.5);
        @include border-radius(50%);
        font-size: 0;
        position: relative;
        z-index: 1;
        &::after {
          position: absolute;
          content: '';
          top: 3px;
          left: 3px;
          width: calc(100% - 6px);
          height: calc(100% - 6px);
          background-color: #fff;
          opacity: 0.65;
          @include border-radius(50%);
          z-index: -1; } } } } }
.hero {
  &__title {
    text-transform: uppercase;
    font-size: rem(100px);
    line-height: 1;
    font-weight: 700;
    letter-spacing: -5px;
    text-shadow: -2px 10px 5px rgba(#000, 0.15);
    @include media(1199px) {
      font-size: rem(72px); }
    @include media(767px) {
      font-size: rem(56px); } } }

@include keyframes(music) {
	0% {
		height: 2px; }
	50% {
		height: 20px; }
	100% {
		height: 5px; } }

@mixin equalize($number) {
	animation: music #{$number}s linear infinite; }
.equalizer {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 20px;
  overflow: hidden;
  z-index: -1;
  &.style--two {
    .equalizer-item {
      width: 15px; } } }
.equalizer-item {
  display: inline-block;
  // background: $base-color
  @include gradient-three;
  width: 5px;
  height: 20px;
  transition: all linear 0.1s; }
.equalizer-none {
	opacity: 0; }
.equalizer-1 {
	@include equalize(.5); }
.equalizer-2 {
	@include equalize(.9); }

.equalizer-3 {
	@include equalize(.7); }

.equalizer-4 {
	@include equalize(.9); }

.equalizer-5 {
	@include equalize(.8); }

.equalizer-6 {
	@include equalize(.7); }

.equalizer-7 {
	@include equalize(.5); }

.equalizer-8 {
	@include equalize(.9); }

.equalizer-9 {
	@include equalize(.4); }

.equalizer-10 {
	@include equalize(.9); }

.equalizer-11 {
	@include equalize(.5); }

.equalizer-12 {
	@include equalize(.7); }

.equalizer-item {
  -webkit-animation-play-state: running;
  animation-play-state: running; }
/* hero section css end */


/* inner hero section css start */
.inner-hero {
  padding-top: rem(150px);
  padding-bottom: rem(55px);
  position: relative;
  z-index: 1;
  &::after {
    @include positionTwo;
    @include gradient-three;
    z-index: -1;
    background: $base-color;
    background: -webkit-linear-gradient(to bottom, rgba($base-color-two, 0.15), rgb(108,0,146));
    background: linear-gradient(to bottom, rgba($base-color-two, 0.15), rgb(108,0,146));
    opacity: 0.75; } }
.page-title {
  font-size: rem(42px);
  @include media(1199px) {
    font-size: rem(36px); }
  @include media(767px) {
    font-size: rem(32px); } }
/* inner hero section css end */
