/* online section css start */
.section-shape {
  position: absolute;
  top: 50%;
  @include transform(translateY(-50%));
  z-index: -1; }
.online-section {
  @include transition(all 0.5s);
  .equalizer {} }

.main-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  > section,
  > div {
    order: 1;
    width: 100%; }
  &.active {
    .online-section {
      .audio-player {
        position: fixed;
        right: 0;
        bottom: 100px;
        z-index: 99999;
        .play-container {
          margin-top: 0;
          margin-right: 0;
          transform: translateY(0);
          .music-wave {
            top: -4px;
            left: -4px; } }
        .timeline {
          display: none; }
        .right {
          display: none; }
        .volume-container {
          display: none; } } } } }

.audio-player {
  .timeline {
    background: white;
    width: 100%;
    height: 5px;
    position: relative;
    cursor: pointer;
    box-shadow: 0 2px 10px 0 #0008;
    width: calc(100% - 85px);
    margin-left: 85px;
    margin-bottom: -60px;
    .progress {
      background: $base-color;
      width: 0%;
      height: 100%;
      transition: 0.25s; } }
  .controls {
    display: flex;
    .toggle-play {
      &.play {
        cursor: pointer;
        position: relative;
        left: 10px;
        height: 0;
        width: 0;
        border: 12px solid #0000;
        border-left: 18px solid white;

        &:hover {
          transform: scale(1.1); } }
      &.pause {
        height: 15px;
        width: 20px;
        cursor: pointer;
        position: relative;
        left: 5px;
        top: -3px;

        &:before {
          position: absolute;
          top: 0;
          left: 0px;
          background: white;
          content: "";
          height: 20px;
          width: 3px; }

        &:after {
          position: absolute;
          top: 0;
          right: 8px;
          background: white;
          content: "";
          height: 20px;
          width: 3px; }

        &:hover {
          transform: scale(1.1); } } }
    .time {
      display: flex;

      > * {
        padding: 2px; } }

    .volume-container {
      cursor: pointer;
      position: relative;
      z-index: 2;
      .volume-button {
        height: 26px;
        display: flex;
        align-items: center;

        .volume {
          transform: scale(0.7); } }
      .volume-slider {
        position: absolute;
        left: -3px;
        top: 15px;
        z-index: -1;
        width: 0;
        height: 15px;
        background: white;
        box-shadow: 0 0 20px #000a;
        transition: 0.25s;

        .volume-percentage {
          background: coral;
          height: 100%;
          width: 75%; } }

      &:hover .volume-slider {
        left: -123px;
        width: 120px; } } } }
.controls {
  display: flex;
  align-items: flex-end;
  .right {
    height: 60px;
    width: calc(100% - 35px);
    padding-left: 65px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include gradient-three;
    @include media(480px) {
      flex-direction: column; } } }
.play-container {
  width: 150px;
  height: 150px;
  background-color: transparent;
  @include border-radius(50%);
  margin-top: -30px;
  margin-right: -53px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  @include transform(translateY(42px));
  @include media(767px) {
    width: 100px;
    height: 100px;
    margin-top: 18px;
    @include transform(translate(15px, 15px)); }
  &::after {
    position: absolute;
    content: '';
    width: 100px;
    height: 100px;
    border: 2px solid $base-color;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -50px;
    background-color: darken($base-color-two, 3%);
    @include border-radius(50%);
    @include media(767px) {
      width: 80px;
      height: 80px;
      margin-left: -40px;
      margin-top: -40px; } }
  .music-wave {
    position: absolute;
    top: 0;
    left: 0; }
  .toggle-play {
    position: relative;
    z-index: 2;
    &.pause {
      ~ .music-wave {
          animation: musicShake 1s infinite linear; } } } }

@include keyframes(musicShake) {
  0% {
    @include transform(scale(0.75)); }
  25% {
    @include transform(scale(1.01)); }
  50% {
    @include transform(scale(0.72)); }
  50% {
    @include transform(scale(0.55)); }
  100% {
    @include transform(scale(1)); } }
/* online section css end */


/* schedule section css start */
.schedule-card {
  &:hover {
    .schedule-btn {
      opacity: 1;
      visibility: visible;
      bottom: -6px;
      right: -6px; } }
  &__thumb {
    position: relative;
    height: 245px;
    img {
      width: 100%;
      height: 100%;
      @include object-fit;
      @include border-radius(8px); }
    &::before {
      @include positionTwo;
      @include gradient-three;
      opacity: 0.5;
      @include border-radius(8px); }
    .overlay-content {
      position: absolute;
      top: 50%;
      width: 100%;
      padding: 15px 20px;
      text-align: center;
      @include transform(translateY(-50%)); }
    .schedule-btn {
      position: absolute;
      bottom: -10px;
      right: -10px;
      width: 40px;
      height: 40px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      background-color: $base-color;
      @include border-radius(50%);
      z-index: 2;
      opacity: 0;
      visibility: hidden;
      @include transition(all 0.3s); } }
  .rj {
    @include d-flex;
    align-items: center;
    margin-top: -50px;
    position: relative;
    z-index: 1;
    padding-left: 20px;
    .thumb {
      width: 70px;
      height: 70px;
      overflow: hidden;
      border: 2px solid #fff;
      @include border-radius(50%); }
    .content {
      width: calc(100% - 100px);
      padding-left: 20px;
      padding-bottom: 20px; } } }
/* schedule section css end */


/* event section css start */
.event-card {
  @include d-flex;
  padding: rem(15px);
  background-color: rgba($base-color-two, 0.45);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba($base-color, 0.45);
  @extend %transition;
  &:hover {
    box-shadow: 0 3px 15px 1px rgba($base-color, 0.25);
    @include transform(translateY(-5px)); }
  .thumb {
    width: 150px;
    min-height: 124px;
    @include media(480px) {
      width: 100%; }
    img {
      height: 100%;
      width: 100%;
      @include object-fit; } }
  .content {
    width: calc(100% - 300px);
    padding-left: rem(25px);
    @include media(767px) {
      width: calc(100% - 150px); }
    @include media(480px) {
      width: 100%;
      padding-left: 0;
      margin-top: 20px; } }
  .right {
    width: 150px;
    text-align: right;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @include media(767px) {
      width: 100%;
      justify-content: center;
      margin-top: 20px; }
    @include media(480px) {
      justify-content: flex-start; } } }
.event-details-list {
  li + li {
    margin-top: 5px; }
  li {
    font-size: rem(14px);
    @include d-flex;
    .caption {
      width: 40%;
      font-weight: 600;
      position: relative;
      &::after {
        position: absolute;
        content: ':';
        top: 0;
        right: 0; } }
    .value {
      width: 60%;
      padding-left: 10px; } } }
.event-card-two {
  position: relative;
  overflow: hidden;
  @include border-radius(8px);
  &::after {
    @include positionTwo;
    @include gradient-three;
    opacity: 0.45; }
  .thumb {
    height: 310px;
    img {
      height: 100%;
      width: 100%;
      @include object-fit; } }
  .content {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 30px;
    z-index: 1; } }
/* event section css end */


/* rj section css start */
.rj-single {
  @include border-radius(8px);
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 15px 2px rgba($base-color, 0.45); }
  &:hover {
    .thumb {
      .social-links {
        right: 20px; } } }
  .thumb {
    @include border-radius(8px);
    overflow: hidden;
    position: relative;
    z-index: 1;
    &::after {
      @include positionTwo;
      background: $base-color;
      background: -webkit-linear-gradient(to bottom, rgba($base-color-two, 0.15), rgb(108,0,146));
      background: linear-gradient(to bottom, rgba($base-color-two, 0.15), rgb(108,0,146)); }
    img {
      width: 100%; }
    .overlay-content {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: 30px;
      z-index: 1; }
    .social-links {
      position: absolute;
      z-index: 2;
      top: 20px;
      right: -45px;
      @include transition(all 0.3s);
      li {
        margin: 5px;
        a {
          width: 35px;
          height: 35px;
          background-color: $base-color;
          color: #fff;
          display: flex;
          justify-content: center;
          align-items: center;
          @include border-radius(3px); } } } } }
/* rj section css end */

/* profile section css start */
.profile-wrapper {
  background-color: rgba($base-color-two, 0.45);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(10px);
  margin-top: 150px;
  padding: rem(50px);
  border: 1px solid rgba(#fff, 0.25);
  @include border-radius(8px);
  box-shadow: 0 3px 5px 1px rgba(#000, 0.15);
  @include media(480px) {
    padding: rem(30px); }
  .thumb {
    position: relative;
    @include border-radius(8px);
    overflow: hidden;
    margin-top: -200px;
    box-shadow: 0 5px 15px 1px rgba($base-color, 0.3);
    &::after {
      @include positionTwo;
      background: $base-color;
      background: -webkit-linear-gradient(to bottom, rgba($base-color-two, 0.15), rgba(108,0,146, 0.65));
      background: linear-gradient(to bottom, rgba($base-color-two, 0.15), rgba(108,0,146, 0.65)); }
    img {
      width: 100%; } } }
.resume-card {
  @include d-flex;
  position: relative;
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: 5px;
    left: 23px;
    width: 1px;
    height: calc(100% - 8px);
    background-color: rgba(#fff, 0.15);
    z-index: -1; }
  .icon {
    width: 50px;
    height: 50px;
    background-color: $base-color;
    @include border-radius(50%);
    color: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(24px);
    margin-top: 5px; }
  .content {
    width: calc(100% - 50px);
    padding-left: 20px;
    .title {
      .inner-title {
        font-family: $para-font;
        font-size: 14px;
        color: $para-color; } } } }
/* profile section css end */

/* brand section css start */
.brand-section {
  padding-top: 50px;
  padding-bottom: 50px; }
.brand-slider {
  .slick-list {
    margin: 0 -15px; }
  .single-slide {
    margin: 0 15px; } }
.brand-item {
  height: 120px;
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: rgba($base-color-two, 0.15);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 2px solid rgba(#fff, 0.15);
  @include border-radius(5px);
  img {
    max-height: 90px; } }
/* brand section css end */

/* contact section css start */
.contact-wrapper {
  padding: rem(50px);
  background-color: rgba($base-color-two, 0.25);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 2px solid rgba(#fff, 0.15);
  @include border-radius(10px);
  @include media(480px);
  background-image: {
    padding: rem(30px); } }
.contact-item {
  @include d-flex;
  align-items: center;
  .icon {
    width: 55px;
    height: 55px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    font-size: rem(36px);
    color: #fff;
    background-color: $base-color;
    line-height: 1;
    @include border-radius(3px); }
  .content {
    width: calc(100% - 55px);
    padding-left: 15px;
    .caption {
      font-size: rem(16px);
      margin-bottom: 5px;
      @include media(380px) {
        font-size: rem(14px); } }
    p {
      font-size: rem(20px);
      word-break: break-word;
      @include media(380px) {
        font-size: rem(18px); }
      a {
        color: $para-color;
        &:hover {
          color: $base-color; } } } } }
/* contact section css end */
