@import "./_reset";
/* global css strat */
@import "./_color";
@import "./_bg";
// margin global css
.mb-30 {
  margin-bottom: 30px; }
.mb-none-30 {
  margin-bottom: -30px; }

// padding global css
.pt-50 {
  padding-top: 50px; }
.pb-50 {
  padding-bottom: 50px; }

.pt-100 {
  padding-top: 100px; }
.pb-100 {
  padding-bottom: 100px; }
.bg_img {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }
.glass--bg {
  background-color: rgba($base-color, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px); }
.glass--bg-two {
  background-color: rgba($base-color-two, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px); }
.glass--bg-three {
  background-color: rgba(#ffffff, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px); }
.z-index2 {
  z-index: 2; }
.opacity--10 {
  opacity: 0.1; }
.opacity--20 {
  opacity: 0.2; }
.opacity--30 {
  opacity: 0.3; }
.opacity--50 {
  opacity: 0.5; }
.overlay--one {
  position: relative;
  z-index: 1;
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.45;
    z-index: -1; } }

.overlay--two {
  position: relative;
  z-index: 1;
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.65;
    z-index: -1; } }

.slick-arrow {
  cursor: pointer; }

.section-header {
  margin-bottom: rem(65px);
  position: relative; }
.section-title {
  font-size: rem(52px);
  @media (max-width: 1199px) {
    font-size: rem(48px); }
  @media (max-width: 991px) {
    font-size: rem(36px); } }
a.text-white {
  &:hover {
    color: $base-color !important; } }
.custom--accordion {
  .accordion-item + .accordion-item {
    margin-top: rem(20px); }
  .accordion-item {
    border: 1px solid rgba($base-color, 0.5);
    @include border-radius(5px);
    &:first-child {
      .accordion-button {
        border-top: none; } }
    &:last-child {
      .accordion-button {
        border-bottom: none; } } }
  .accordion-button {
    color: #ffffff;
    padding: rem(20px) rem(25px);
    background-color: rgba($base-color, 0.15);
    font-size: rem(18px);
    position: relative;
    text-align: left;
    &::after {
      position: absolute;
      top: rem(20px);
      right: rem(13px);
      font-size: rem(17px);
      content: '\f107';
      font-family: 'Line Awesome Free';
      font-weight: 900;
      background-image: none;
      color: #ffffff; }
    &:not(.collapsed) {
      background-color: $base-color;
      color: #000;
      &::after {
        color: #000; } }
    &:focus {
      box-shadow: none;
      outline: none;
      border-color: transparent; } }
  .accordion-body {
    padding: rem(20px) rem(25px); } }
.custom--tab {
  border-bottom: none;
  .nav-item {
    margin: 5px 10px;
    .nav-link {
      background-color: $base-color-two;
      color: #fff;
      @include border-radius(3px);
      border: 1px solid rgba(#fff, 0.25);
      &.active {
        background-color: $base-color;
        border-color: $base-color; } } } }

.page-breadcrumb {
  @include d-flex;
  margin-top: rem(15px);
  li {
    color: rgba(#ffffff, 0.8);
    text-transform: capitalize;
    &::after {
      content: '-';
      color: #ffffff;
      margin: 0 rem(5px); }
    &:first-child {
      &::before {
        content: "\f015";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $base-color;
        margin-right: rem(6px); } }
    &:last-child {
      &::after {
        display: none; } }
    a {
      color: #ffffff;
      text-transform: capitalize;
      &:hover {
        color: $base-color; } } } }
%transition {
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s; }
.progress-bar {
  background-color: $base-color; }
.video-btn {
  position: relative;
  width: rem(105px);
  height: rem(105px);
  color: #ffffff;
  @include border-radius(50%);
  display: inline-block;
  background-color: $base-color;
  text-align: center;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  @include animation(pulse 2000ms linear infinite);
  &::before, &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 74px;
    background-color: $base-color;
    opacity: 0.15;
    z-index: -10; }
  &::before {
    z-index: -10;
    @include animation(inner-ripple 2000ms linear infinite); }
  &::after {
    z-index: -10;
    @include animation(outer-ripple 2000ms linear infinite); }
  i {
    font-size: 36px; }
  &.video-btn--sm {
    width: rem(70px);
    height: rem(70px); } }
@include keyframes(outer-ripple) {
  0% {
    @include transform(scale(1));
    opacity: 0.5; }
  80% {
    @include transform(scale(1.5));
    opacity: 0; }
  100% {
    @include transform(scale(2.5));
    opacity: 0; } }

@include keyframes(inner-ripple) {
  0% {
    @include transform(scale(1));
    opacity: 0.5; }
  30% {
    @include transform(scale(1));
    opacity: 0.5; }
  100% {
    @include transform(scale(1.5));
    opacity: 0; } }
.object-fit--cover {
  @include object-fit; }
.cursor {
  position: fixed;
  background-color: $base-color;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  z-index: 1;
  transition: 0.3s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform, 0.2s cubic-bezier(0.75, -0.27, 0.3, 1.33) opacity;
  user-select: none;
  pointer-events: none;
  z-index: 10000;
  transform: scale(1);
  &.active {
    opacity: 1;
    transform: scale(0); }
  &.menu-active {
    opacity: 1;
    transform: scale(0); }
  &.hovered {
    opacity: 1; } }

.cursor-follower {
  position: fixed;
  border: 1px solid $base-color;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  z-index: 1;
  transition: 0.6s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform, 0.2s cubic-bezier(0.75, -0.27, 0.3, 1.33) opacity, 0.2s cubic-bezier(0.75, -0.27, 0.3, 1.33) background;
  user-select: none;
  pointer-events: none;
  z-index: 10000;
  transform: translate(2px, 2px);
  &.active {
    opacity: 1;
    transform: scale(2); }
  &.menu-active {
    opacity: 1;
    transform: scale(2); }
  &.hovered {
    opacity: 1; } }

.overlay {
  z-index: 10;
  background-color: #222;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }
.overlay-loader {
  width: 50px;
  height: 50px;
  background-color: #bada55; }

.pagination {
  margin: rem(-5px) rem(-7px);
  .page-item {
    margin: rem(5px) rem(7px);
    &.active {
      .page-link {
        background-color: $base-color;
        color: #000; } }
    .page-link {
      width: rem(45px);
      height: rem(45px);
      display: flex;
      justify-content: center;
      align-items: center;
      @include border-radius(5px);
      border: 1px solid rgba($base-color, 0.25);
      @extend .glass--bg;
      color: #ffffff; } } }

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: $base-color-two;
  z-index: 9999999;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  .preloader-img {
    max-width: 90px; }
  &__inner {
    .logo-area {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-end;
      position: relative;
      .equalizer {
        transform: translateX(0);
        left: 15px;
        z-index: 1;
        bottom: 2px;
        .equalizer-item {
          background-color: $base-color-two; } }
      .logo-name {
        color: $base-color;
        font-size: rem(36px);
        padding-left: 20px; } }
    .loading-bar {
      width: 100%;
      height: 3px;
      background-color: darken($base-color-two, 3%);
      margin-top: 30px;
      position: relative;
      overflow: hidden;
      &::after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 20px;
        height: 100%;
        background-color: $base-color;
        animation: leftRight 4s infinite linear; } } } }
@include keyframes(leftRight) {
  0% {
    left: 0; }
  50% {
    left: 100%; }
  100% {
    left: 0; } }
// animation css
.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp; }
@include keyframes (fadeInUp) {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
    -ms-transform: translateY(20px);
    transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0); } }
.fadeInLeft {
  -webkit-animation-name: fadeInLeft;
  animation-name: fadeInLeft; }
@include keyframes (fadeInLeft) {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
    -ms-transform: translateX(-20px);
    transform: translateX(-20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0); } }
.fadeInRight {
  -webkit-animation-name: fadeInRight;
  animation-name: fadeInRight; }
@include keyframes (fadeInRight) {
  0% {
    opacity: 0;
    -webkit-transform: translateX(20px);
    -ms-transform: translateX(20px);
    transform: translateX(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0); } }
/* global css end */
